      </scroll-view>
    </view>

    <!-- ÂÖ®ÈÄâÊìç‰Ωú -->
    <view class="select-all-section">
      <view class="select-all-item" @click="toggleSelectAll">
        <view class="checkbox" :class="{ checked: isAllSelected }">
          <text v-if="isAllSelected" class="check-icon">‚úì</text>
        </view>
        <text class="select-all-text">ÂÖ®ÈÄâÂΩìÂâçÂàÜÁ±ª ({{ selectedCategories.length }}/{{ filteredSubCategories.length }})</text>
      </view>
      <view class="clear-btn" @click="clearAllSelection">
        <text>Ê∏ÖÁ©∫</text>
      </view>
    </view>

    <!-- Â≠êÁ±ªÂà´ÂàóË°® -->
    <scroll-view class="category-list" scroll-y>
      <view v-if="filteredSubCategories.length === 0" class="empty-state">
        <text class="empty-icon">üìÇ</text>
        <text class="empty-text">{{ searchKeyword ? 'Êú™ÊâæÂà∞ÂåπÈÖçÁöÑÁ±ªÂà´' : 'ËØ•ÂàÜÁ±ª‰∏ãÊöÇÊó†Â≠êÁ±ªÂà´' }}</text>
      </view>
      
      <view 
        v-for="subCategory in filteredSubCategories" 
        :key="subCategory.id" 
        class="category-item"
        @click="toggleCategory(subCategory)"
      >
        <view class="category-info">
          <view class="checkbox" :class="{ checked: isCategorySelected(subCategory) }">
            <text v-if="isCategorySelected(subCategory)" class="check-icon">‚úì</text>
          </view>
          <view class="category-content">
            <text class="category-name">{{ subCategory.name }}</text>
            <text class="category-desc">{{ subCategory.description || 'ÊöÇÊó†ÊèèËø∞' }}</text>
            <view class="category-tags">
              <text class="tag">{{ subCategory.parentName }}</text>
              <text v-if="subCategory.code" class="tag code-tag">{{ subCategory.code }}</text>
            </view>
          </view>
        </view>
        <view class="category-status">
          <text class="status-text" :class="subCategory.status">{{ getStatusText(subCategory.status) }}</text>
        </view>
      </view>
    </scroll-view>

    <!-- Â∫ïÈÉ®Êìç‰ΩúÊ†è -->
    <view class="footer">
      <view class="selected-info">
        <text class="selected-text">Â∑≤ÈÄâÊã© {{ selectedCategories.length }} ‰∏™Á±ªÂà´</text>
      </view>
      <view class="footer-actions">
        <view class="action-btn cancel-btn" @click="cancelSelection">ÂèñÊ∂à</view>
        <view class="action-btn confirm-btn" @click="confirmSelection">Á°ÆÂÆö</view>
      </view>
    </view>
  </view>
</template>

<script>
import PageHeader from '@/components/page-header/page-header.vue'

export default {
  components: {
    PageHeader
  },
  
  data() {
    return {
      searchKeyword: '',
      selectedCategories: [],
      currentCategoryId: 1,
      allCategories: [
        {
          id: 1,
          name: 'ÂéüÊùêÊñô',
          children: [
            { id: 11, name: 'Ê≠£ÊûÅÊùêÊñô', description: 'ÈîÇÁîµÊ±†Ê≠£ÊûÅÊ¥ªÊÄßÊùêÊñô', code: 'PM001', status: 'active', parentName: 'ÂéüÊùêÊñô' },
            { id: 12, name: 'Ë¥üÊûÅÊùêÊñô', description: 'ÈîÇÁîµÊ±†Ë¥üÊûÅÊ¥ªÊÄßÊùêÊñô', code: 'NM001', status: 'active', parentName: 'ÂéüÊùêÊñô' },
            { id: 13, name: 'ÁîµËß£Ê∂≤', description: 'ÈîÇÁîµÊ±†ÁîµËß£Ê∂≤', code: 'EL001', status: 'active', parentName: 'ÂéüÊùêÊñô' },
            { id: 14, name: 'ÈöîËÜú', description: 'ÈîÇÁîµÊ±†ÈöîËÜúÊùêÊñô', code: 'SP001', status: 'active', parentName: 'ÂéüÊùêÊñô' },
            { id: 15, name: 'ÈõÜÊµÅ‰Ωì', description: 'ÈìúÁÆî„ÄÅÈìùÁÆîÁ≠âÈõÜÊµÅ‰Ωì', code: 'CC001', status: 'inactive', parentName: 'ÂéüÊùêÊñô' }
          ]
        },
        {
          id: 2,
          name: 'ÂçäÊàêÂìÅ',
          children: [
            { id: 21, name: 'Ê≠£ÊûÅÁâá', description: 'Ê∂ÇÂ∏ÉÂêéÁöÑÊ≠£ÊûÅÁâá', code: 'PS001', status: 'active', parentName: 'ÂçäÊàêÂìÅ' },
            { id: 22, name: 'Ë¥üÊûÅÁâá', description: 'Ê∂ÇÂ∏ÉÂêéÁöÑË¥üÊûÅÁâá', code: 'NS001', status: 'active', parentName: 'ÂçäÊàêÂìÅ' },
            { id: 23, name: 'ÁîµËäØ', description: 'Âç∑ÁªïÂêéÁöÑÁîµËäØ', code: 'CE001', status: 'active', parentName: 'ÂçäÊàêÂìÅ' },
            { id: 24, name: 'Ê®°ÁªÑ', description: 'ÁªÑË£ÖÂêéÁöÑÁîµÊ±†Ê®°ÁªÑ', code: 'MD001', status: 'active', parentName: 'ÂçäÊàêÂìÅ' }
          ]
        },
        {
          id: 3,
          name: 'ÊàêÂìÅ',
          children: [
            { id: 31, name: 'ÂúÜÊü±ÁîµÊ±†', description: '18650„ÄÅ21700Á≠âÂúÜÊü±ÁîµÊ±†', code: 'CB001', status: 'active', parentName: 'ÊàêÂìÅ' },
            { id: 32, name: 'ÊñπÂΩ¢ÁîµÊ±†', description: 'ÊñπÂΩ¢Á°¨Â£≥ÁîµÊ±†', code: 'PB001', status: 'active', parentName: 'ÊàêÂìÅ' },
            { id: 33, name: 'ËΩØÂåÖÁîµÊ±†', description: 'ËΩØÂåÖË£ÖÁîµÊ±†', code: 'PO001', status: 'active', parentName: 'ÊàêÂìÅ' },
            { id: 34, name: 'ÁîµÊ±†ÂåÖ', description: 'ÂÆåÊï¥ÁöÑÁîµÊ±†ÂåÖÁ≥ªÁªü', code: 'BP001', status: 'active', parentName: 'ÊàêÂìÅ' }
          ]
        },
        {
          id: 4,
          name: 'ËæÖÊñô',
          children: [
            { id: 41, name: 'ÂØºÁîµÂâÇ', description: 'ÊèêÈ´òÂØºÁîµÊÄßÁöÑÊ∑ªÂä†ÂâÇ', code: 'CA001', status: 'active', parentName: 'ËæÖÊñô' },
            { id: 42, name: 'Á≤òÁªìÂâÇ', description: 'ÁîµÊûÅÊùêÊñôÁ≤òÁªìÂâÇ', code: 'BI001', status: 'active', parentName: 'ËæÖÊñô' },
            { id: 43, name: 'Ê∫∂ÂâÇ', description: 'ÊµÜÊñôÂà∂Â§áÊ∫∂ÂâÇ', code: 'SO001', status: 'active', parentName: 'ËæÖÊñô' },
            { id: 44, name: 'Ê∑ªÂä†ÂâÇ', description: 'ÁîµËß£Ê∂≤Ê∑ªÂä†ÂâÇ', code: 'AD001', status: 'inactive', parentName: 'ËæÖÊñô' }
          ]
        },
        {
          id: 5,
          name: 'ÂåÖË£ÖÊùêÊñô',
          children: [
            { id: 51, name: 'ÈìùÂ°ëËÜú', description: 'ËΩØÂåÖÁîµÊ±†Â§ñÂåÖË£Ö', code: 'AF001', status: 'active', parentName: 'ÂåÖË£ÖÊùêÊñô' },
            { id: 52, name: 'Èí¢Â£≥', description: 'ÂúÜÊü±ÁîµÊ±†Èí¢Â£≥', code: 'SC001', status: 'active', parentName: 'ÂåÖË£ÖÊùêÊñô' },
            { id: 53, name: 'ÈìùÂ£≥', description: 'ÊñπÂΩ¢ÁîµÊ±†ÈìùÂ£≥', code: 'AC001', status: 'active', parentName: 'ÂåÖË£ÖÊùêÊñô' },
            { id: 54, name: 'Ê†áÁ≠æ', description: '‰∫ßÂìÅÊ†áËØÜÊ†áÁ≠æ', code: 'LB001', status: 'active', parentName: 'ÂåÖË£ÖÊùêÊñô' }
          ]
        }
      ]
    }
  },

  computed: {
    mainCategories() {
      return this.allCategories
    },

    currentCategory() {
      return this.allCategories.find(cat => cat.id === this.currentCategoryId)
    },

    currentSubCategories() {
      return this.currentCategory ? this.currentCategory.children || [] : []
    },

    filteredSubCategories() {
      if (!this.searchKeyword.trim()) {
        return this.currentSubCategories
      }
      
      const keyword = this.searchKeyword.toLowerCase()
      let filtered = []
      
      // Â¶ÇÊûúÊúâÊêúÁ¥¢ÂÖ≥ÈîÆËØçÔºåÊêúÁ¥¢ÊâÄÊúâÂàÜÁ±ª
      this.allCategories.forEach(category => {
        if (category.children) {
          const matchedChildren = category.children.filter(child =>
            child.name.toLowerCase().includes(keyword) ||
            (child.description && child.description.toLowerCase().includes(keyword)) ||
            (child.code && child.code.toLowerCase().includes(keyword))
          )
          filtered = filtered.concat(matchedChildren)
        }
      })
      
      return filtered
    },

    isAllSelected() {
      return this.filteredSubCategories.length > 0 && 
             this.filteredSubCategories.every(cat => this.isCategorySelected(cat))
    }
  },

  onLoad(options) {
    // Â¶ÇÊûúÊúâÈ¢ÑÈÄâÁöÑÁ±ªÂà´ÔºåÊÅ¢Â§çÈÄâÊã©Áä∂ÊÄÅ
    if (options.selected) {
      try {
        const selected = JSON.parse(decodeURIComponent(options.selected))
        this.selectedCategories = selected || []
      } catch (e) {
        console.error('Ëß£ÊûêÈ¢ÑÈÄâÁ±ªÂà´Â§±Ë¥•:', e)
      }
    }
  },

  methods: {
    onSearchInput() {
      // ÊêúÁ¥¢Êó∂Ëá™Âä®ÂàáÊç¢Âà∞ÊòæÁ§∫ÊâÄÊúâÁªìÊûú
    },

    clearSearch() {
      this.searchKeyword = ''
    },

    switchCategory(category) {
      this.currentCategoryId = category.id
      this.searchKeyword = '' // ÂàáÊç¢ÂàÜÁ±ªÊó∂Ê∏ÖÁ©∫ÊêúÁ¥¢
    },

    isCategorySelected(category) {
      return this.selectedCategories.some(selected => selected.id === category.id)
    },

    toggleCategory(category) {
      const index = this.selectedCategories.findIndex(selected => selected.id === category.id)
      if (index > -1) {
        this.selectedCategories.splice(index, 1)
      } else {
        this.selectedCategories.push(category)
      }
    },

    toggleSelectAll() {
      if (this.isAllSelected) {
        // ÂèñÊ∂àÂÖ®ÈÄâÂΩìÂâçÁ≠õÈÄâÁöÑÁ±ªÂà´
        this.selectedCategories = this.selectedCategories.filter(selected => 
          !this.filteredSubCategories.some(filtered => filtered.id === selected.id)
        )
      } else {
        // ÂÖ®ÈÄâÂΩìÂâçÁ≠õÈÄâÁöÑÁ±ªÂà´
        this.filteredSubCategories.forEach(category => {
          if (!this.isCategorySelected(category)) {
            this.selectedCategories.push(category)
          }
        })
      }
    },

    clearAllSelection() {
      this.selectedCategories = []
    },

    getStatusText(status) {
      return status === 'active' ? 'ÂêØÁî®' : 'ÂÅúÁî®'
    },

    cancelSelection() {
      uni.navigateBack()
    },

    confirmSelection() {
      // Ëé∑Âèñ‰∏ä‰∏ÄÈ°µÁöÑÂÆû‰æãÂπ∂ËÆæÁΩÆËøîÂõûÊï∞ÊçÆ
      const pages = getCurrentPages()
      const prevPage = pages[pages.length - 2]

      if (prevPage) {
        if (!prevPage.data) {
          prevPage.data = {}
        }
        prevPage.data.selectedCategories = [...this.selectedCategories]
      }

      uni.navigateBack()
    }
  }
}
</script>

